.modal-parent{
    position: fixed;
    top: 50%;
    left: 50%;
    transform: translate(-50% , -50%);
    z-index: 2;
    width: 100%;
    height: 100vh;
    background-color: rgba(0, 0, 0, 0.5);
    display: flex;
    align-items: center;
    justify-content: center;
    opacity: 0;
    visibility: hidden;
    transition: 0.4s;
    -webkit-transition: 0.4s;
    -moz-transition: 0.4s;
    -ms-transition: 0.4s;
    -o-transition: 0.4s;
    -webkit-transform: translate(-50% , -50%);
    -moz-transform: translate(-50% , -50%);
    -ms-transform: translate(-50% , -50%);
    -o-transform: translate(-50% , -50%);
} 




.modal-parent.active {
    opacity: 1;
    visibility: visible;
}

.delete-modal{
    background-color: var(--white);
    padding: 50px;
    text-align: center;
}

.delete-modal h1 {
    font-size: 2rem;

}

.delete-modal-btns{
    display: flex;
    column-gap: 20px ;
}

.delete-btn {
    width: 100%;
    margin-top: 30px;
    font-size: 1.3rem;
    padding: 10px 0;
    border: none;
    outline: none;
    cursor: pointer;
}

.delete-modal-accept-btn {
    background-color: var(--blue);
    color: var(--white);
    width: 100%;
}