.edit-user-info-input-group {
    display: flex;
    align-items: center;
    column-gap: 10px;
    width: 80%;
    background-color: #f4f4f4;
    border-radius: 10px;
    -webkit-border-radius: 10px;
    -moz-border-radius: 10px;
    -ms-border-radius: 10px;
    -o-border-radius: 10px;
    padding: 0 20px;
    margin-top: 20px;
}

.edit-user-info-input {
    outline: none;
    border: none;
    background-color: unset;
    padding: 8px 10px;
    font-size: 1.1rem;
    width: 100%;
}