.products-main{
    margin-top: 60px;

}

.products-title{
    font-size: 2rem;
}

.add-products-form{
    margin-top: 30px;
    background: var(--white);
    display: flex;
    flex-direction: column;
    align-items: flex-end;
    padding: 20px;
    border-radius: 20px;
    -webkit-border-radius: 20px;
    -moz-border-radius: 20px;
    -ms-border-radius: 20px;
    -o-border-radius: 20px;
}

.add-products-form-wrap{
    width: 100%;
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 10px 15px;
}

.add-products-form-group{
    display: flex;
    align-items: center;
    margin: 0 10px;
    column-gap: 10px;
    width: 80%;
    background-color: #f4f4f4;
    padding: 0 20px;
    border-radius: 10px;
    -webkit-border-radius: 10px;
    -moz-border-radius: 10px;
    -ms-border-radius: 10px;
    -o-border-radius: 10px;
}

.add-products-input{
    outline: none;
    border: none;
    background: unset;
    padding: 8px 10px;
    font-size: 1.1rem;
    width: 100%;
}

.add-products-submit{
    background-color: var(--blue);
    color: var(--white);
    margin-top: 10px;
    padding: 10px 20px;
    font-size: 1.1rem;
    border-radius: 10px;
    -webkit-border-radius: 10px;
    -moz-border-radius: 10px;
    -ms-border-radius: 10px;
    -o-border-radius: 10px;
    cursor: pointer;
}